import React from 'react'
export default function Experience({ educations, resumeExperience, resumeBasicInfo }) {
	return (
		<section id="resume" class="section scrollspy">
			<h3 class="page-title white-text teal">Experience</h3>
			<div class="container">
				<div class="row">
					<div class="col s12 m6 l6">
						{resumeExperience && resumeExperience.map((work, i) => (
							<div class="card">
								<div class="card-content">
									<p>
										<span class="card-title"><a href={educations && educations[0].url} target="_blank" class="teal-text hoverline">{work.company}</a></span>
									</p>
									<p class="brown-text custom_font">Bangalore, KA, India</p>
									<p>
										<b>Positation: </b>{work.title}{" "}
										<br />
										<b>Duration: </b>{work.years}
									</p>


									<p>
										<b>Technologies used:</b>
										<ul>
											{work.technologies.map((tech, index) => (
												<li key={index}>{tech}</li>
											))}
										</ul>
									</p>
								</div>
							</div>
						))}

					</div>
				</div>
			</div>
		</section>
	)
}
