import React from 'react'

export default function Education({ educations }) {
	console.log("Education..........", educations)
	return (

		<section id="education" class="section scrollspy">
			<h3 class="page-title white-text teal">Education</h3>
			<div class="container">
				<div class="row">
					<div class="col s12 m6 l6">
						<div class="card">
							<div class="card-content">
								<p>
									<span class="card-title"><a href={educations && educations[0].url} target="_blank" class="teal-text hoverline">{educations && educations[0].institution}</a></span>
								</p>
								<p class="brown-text">Kolkata, India</p>
								<p>
									<b>Degree: </b>{educations && educations[0].degree}{" "} {educations && educations[0].years}
									<br />
									<b>CGPA: </b>{educations && educations[0].cgpa}
								</p>

								<ul>
									<p>
										<b>Relevant Courseworks:</b>
										<ul>
											<li>C/C++, JavaScript, Java</li>
											<li>Foundations of Algorithms</li>
											<li>Data Structures</li>
											<li>Computer Networks</li>
											<li>Operating System</li>
											<li>Cloud Computing</li>
										</ul>
									</p>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	)
}
