import React from 'react'

export default function Skills({ sharedSkills, resumeBasicInfo }) {

	if (sharedSkills && resumeBasicInfo) {
		var sectionName = resumeBasicInfo.section_name.skills;
	}

	const skills = (sharedSkills) => {

		return (<div className="col-md-3 ">
			{sharedSkills.map(function (skills, i) {
				return (
					<ul>
						<span class="glyphicon glyphicon-menu-right"></span>
						<li key={i} className="skill-item">
							{skills.name}
						</li>
					</ul>
				);
			})}
		</div>)

	}

	return (
		<section id="skills">
			<div className="col-md-12">
				<div className="col-md-12">
					<h3 class="page-title white-text teal">{sectionName}</h3>
				</div>
				{sharedSkills &&
					<div className='d-flex m-2 justify-content-around'>
						<div>
							<p className='skill-title'>Frontend:</p>
							{skills(sharedSkills.frentend)}
						</div>

						<div>
							<p className='skill-title'>Backend:</p>

							{skills(sharedSkills.backend)}
						</div>
						<div>
							<p className='skill-title'>Tools:</p>

							{skills(sharedSkills.tools)}
						</div>
						<div>
							<p className='skill-title'>Cloud:</p>

							{skills(sharedSkills.cloud)}
						</div>
					</div>}
			</div>
		</section>
	)
}
